#header {
  transition: var(--transition-normal);
  z-index: 997;
  background: none;
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

#header.header-scrolled {
  background: var(--primary-dark);
  padding: 12px 0;
  box-shadow: 0 2px 20px #0000001a;
}

#header .logo {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-size: 32px;
  font-weight: 400;
  line-height: 1;
}

#header .logo a {
  color: var(--text-white);
}

#header .logo img {
  max-height: 60px;
}

@media (width <= 480px) {
  #header .logo img {
    max-height: 48px;
  }
}

.navbar {
  padding: 0;
}

.navbar ul {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navbar li {
  position: relative;
}

.navbar > ul > li {
  padding: 10px 0 10px 24px;
}

.navbar a, .navbar a:focus {
  color: var(--text-light);
  white-space: nowrap;
  transition: var(--transition-fast);
  justify-content: space-between;
  align-items: center;
  padding: 0 3px;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  display: flex;
  position: relative;
}

.navbar a i, .navbar a:focus i {
  margin-left: 5px;
  font-size: 12px;
  line-height: 0;
}

.navbar > ul > li > a:before {
  content: "";
  background-color: var(--text-white);
  visibility: hidden;
  width: 0;
  height: 2px;
  transition: var(--transition-fast);
  position: absolute;
  bottom: -5px;
  left: 0;
}

.navbar a:hover:before, .navbar li:hover > a:before, .navbar .active:before {
  visibility: visible;
  width: 100%;
}

.navbar a:hover, .navbar .active, .navbar .active:focus, .navbar li:hover > a {
  color: var(--text-white);
}

.mobile-nav-toggle {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  font-size: 28px;
  line-height: 0;
  transition: all .5s;
  display: none;
}

@media (width <= 991px) {
  .mobile-nav-toggle {
    display: block;
  }

  .navbar ul {
    display: none;
  }
}

.navbar-mobile {
  z-index: 999;
  background: #1c2f41e6;
  transition: all .3s;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.navbar-mobile .mobile-nav-toggle {
  position: absolute;
  top: 15px;
  right: 15px;
}

.navbar-mobile ul {
  background-color: #fff;
  padding: 10px 0;
  transition: all .3s;
  display: block;
  position: absolute;
  inset: 55px 15px 15px;
  overflow-y: auto;
}

.navbar-mobile > ul > li {
  padding: 0;
}

.navbar-mobile a, .navbar-mobile a:focus {
  color: #2c4964;
  padding: 10px 20px;
  font-size: 15px;
}

.navbar-mobile a:hover:before, .navbar-mobile li:hover > a:before, .navbar-mobile .active:before {
  visibility: hidden;
}

.navbar-mobile a:hover, .navbar-mobile .active, .navbar-mobile li:hover > a {
  color: #191065;
}

.bi-list:before, .bi-x:before {
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: bootstrap-icons;
  font-style: normal;
  line-height: 1;
}

@media (width <= 768px) {
  #header {
    padding: 15px 0;
  }

  #header.header-scrolled {
    padding: 10px 0;
  }

  .navbar-mobile ul {
    padding: 20px 0;
  }

  .navbar-mobile a {
    padding: 15px 20px;
    font-size: 16px;
  }
}

#hero {
  background: var(--bg-dark), url("tpsiteSkylineStock6.37c4aa58.jpg") center center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  display: flex;
  position: relative;
}

#hero .container, #hero .container-fluid {
  padding-top: 84px;
}

#hero h1 {
  color: var(--text-white);
  text-shadow: 2px 2px 4px #0000004d;
  text-align: center;
  max-width: 800px;
  margin: 0 auto 20px;
  font-size: 52px;
  font-weight: 700;
  line-height: 64px;
}

#hero h2 {
  color: var(--text-light);
  text-shadow: 1px 1px 2px #0000004d;
  text-align: center;
  max-width: 800px;
  margin: 10px auto 0;
  font-size: 20px;
}

#hero .btn-get-started {
  letter-spacing: 1px;
  transition: var(--transition-normal);
  color: var(--text-white);
  border: 2px solid var(--text-white);
  text-transform: uppercase;
  background: none;
  border-radius: 5px;
  margin-top: 30px;
  padding: 12px 32px;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
}

#hero .btn-get-started:hover {
  background: var(--primary-light);
  color: var(--text-white);
  border-color: var(--primary-light);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px #2f9c954d;
}

#hero .animated {
  animation: 2s ease-in-out infinite alternate-reverse both up-down;
}

@media (width >= 1200px) {
  #hero {
    background-attachment: fixed;
  }
}

@media (width <= 991px) {
  #hero {
    text-align: center;
  }

  #hero .container, #hero .container-fluid {
    padding-top: 68px;
  }

  #hero .animated {
    animation: none;
  }

  #hero .hero-img {
    text-align: center;
  }

  #hero .hero-img img {
    width: 50%;
  }
}

@media (width <= 768px) {
  #hero h1 {
    font-size: 26px;
    line-height: 36px;
  }

  #hero h2 {
    font-size: 18px;
    line-height: 24px;
  }

  #hero .hero-img img {
    width: 60%;
  }
}

@media (width <= 575px) {
  #hero .hero-img img {
    width: 80%;
  }
}

@media (width <= 575px), (height <= 700px) {
  #hero {
    min-height: 560px;
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(-10px);
  }
}

