:root {
  --primary-color: #191065;
  --primary-light: #2f9c95;
  --primary-dark: #0e3f58;
  --secondary-color: #2c4964;
  --secondary-light: #85ccff;
  --accent-color: #7b27d8;
  --accent-light: #b1a9fc;
  --text-primary: #444;
  --text-secondary: #848484;
  --text-light: #fffc;
  --text-white: #fff;
  --bg-primary: #fff;
  --bg-secondary: #f9f8ff;
  --bg-dark: linear-gradient(45deg, #111b39cc 100%, #cfcaebb3 0%);
  --success-color: #18d26e;
  --error-color: #ed3c0d;
  --warning-color: #ffc107;
  --section-padding: 100px 0;
  --container-padding: 0 15px;
  --transition-fast: .3s ease;
  --transition-normal: .5s ease;
  --transition-slow: .8s ease;
}

body {
  color: var(--text-primary);
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
  line-height: 1.6;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  color: var(--primary-color);
  transition: var(--transition-fast);
  text-decoration: none;
}

a:hover {
  color: var(--primary-light);
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--text-primary);
  margin: 0;
  font-family: Poppins, sans-serif;
}

p {
  color: var(--text-primary);
  margin: 0;
}

.back-to-top {
  visibility: hidden;
  opacity: 0;
  z-index: 996;
  background: var(--primary-light);
  width: 40px;
  height: 40px;
  transition: var(--transition-normal);
  color: var(--text-white);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: fixed;
  bottom: 15px;
  right: 15px;
}

.back-to-top:hover {
  background: var(--secondary-color);
  color: var(--text-white);
}

.back-to-top.active {
  visibility: visible;
  opacity: 1;
}

section {
  padding: var(--section-padding);
  overflow: hidden;
}

.section-bg {
  background-color: var(--bg-secondary);
}

.section-title {
  text-align: center;
  padding-bottom: 30px;
}

.section-title h2 {
  text-transform: uppercase;
  color: var(--text-primary);
  margin-bottom: 20px;
  padding-bottom: 20px;
  font-size: 32px;
  font-weight: bold;
  position: relative;
}

.section-title h2:after {
  content: "";
  background: var(--primary-light);
  border-radius: 2px;
  width: 50px;
  height: 3px;
  display: block;
  position: absolute;
  bottom: 0;
  left: calc(50% - 25px);
}

.section-title p {
  color: var(--text-secondary);
  margin-bottom: 0;
}

@media screen and (width <= 768px) {
  h1, h2, h3, h4, h5, h6 {
    line-height: 1.2;
  }

  section {
    padding: 60px 0;
  }

  .section-title h2 {
    font-size: 24px;
  }
}

@media screen and (width <= 480px) {
  h1 {
    font-size: 28px;
    line-height: 1.1;
  }

  h2 {
    font-size: 20px;
  }

  h3 {
    font-size: 18px;
  }

  p {
    font-size: 14px;
  }
}

a:focus, button:focus, input:focus, textarea:focus {
  outline: 2px solid var(--primary-light);
  outline-offset: 2px;
}

.skip-link {
  background: var(--primary-color);
  color: var(--text-white);
  z-index: 1000;
  border-radius: 4px;
  padding: 8px;
  text-decoration: none;
  position: absolute;
  top: -40px;
  left: 6px;
}

.skip-link:focus {
  top: 6px;
}

@media (prefers-contrast: high) {
  :root {
    --text-primary: #000;
    --text-secondary: #333;
    --primary-color: navy;
    --primary-light: #06c;
  }
}

@media (prefers-reduced-motion: reduce) {
  *, :before, :after {
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

